:root {
  --black: hsla(0, 0%, 0%, 1);
  --bg: hsl(0, 20%, 68%);
  --main-brand: hsl(0, 50%, 60%);
  --activeted-brand: hsl(0, 90%, 60%);
  --acccent-color: hsl(10, 100%, 60%);
  --contrast: hsl(219, 73%, 61%);
  --purple: hsla((266, 100%, 64%, 1));
  --outline-1: red 2px dashed;
}

html,
div,
span,
object,
iframe,
p,
blockquote,
pre,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
i,
center,
dl,
dt,
dd,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

ul,
ol,
menu {
  list-style: none;
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  overflow: hidden;
}

.is-hidden {
  opacity: 0;
  pointer-events: none;
  visibility: hidden;
}

html {
  box-sizing: border-box;
  overflow: auto;
}
body {
  font-family: 'Montserrat', sans-serif;
  background-color: var(--bg);
}

.container {
  width: 320px;
  margin: 0 auto;
  padding: 0 10px;
  outline: var(--outline-1);
  overflow: hidden;
}
